AAmB;AACrB;;AAEA;EACE,qBAAqB;EACrB,0BAA0B;EAC1B,gBAAgB;AAClB;;AAEA;EACE,gCAAgC;AAClC;;AAEA;EACE,aAAa;EACb,iBAAiB;EACjB,oBAAoB;EACpB,eAAe;AACjB;;AAEA;EACE,6BAA6B;EAC7B,YAAY;EACZ,uBAAuB;EACvB,kBAAkB;EAClB,gBAAgB;AAClB;;AAEA;EACE,mCAAmC;AACrC;;AAEA,cAAc;AACd;EACE,aAAa;EACb,sBAAsB;AACxB;;AAEA;EACE,kBAAkB;EAClB,aAAa;EACb,mBAAmB;EACnB,0CAA0C;EAC1C,0BAA0B;EAC1B,YAAY;AACd;;AAEA;EACE,YAAY;EACZ,aAAa;EACb,0CAA0C;EAC1C,cAAc;EACd,kBAAkB;AACpB;;AAEA;EACE,kBAAkB;EAClB,UAAU;EACV,UAAU;EACV,gBAAgB;EAChB,eAAe;AACjB;;AAEA;EACE,mBAAmB;EACnB,eAAe;AACjB;;AAEA;EACE,0BAA0B;EAC1B,iBAAiB;EACjB,kBAAkB;EAClB,qBAAqB;AACvB;;AAEA;EACE,0BAA0B;AAC5B;;AAEA;EACE,qBAAqB;EACrB,kBAAkB;EAClB,iBAAiB;EACjB,uBAAuB;AACzB;;AAEA;EACE,0BAA0B;EAC1B,iBAAiB;AACnB;;AAEA;EACE,qCAAqC;EACrC,WAAW;AACb;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,iBAAiB;AACnB;;AAEA;EACE,kBAAkB;EAClB,aAAa;EACb,+BAA+B;EAC/B,0CAA0C;EAC1C,kBAAkB;EAClB,YAAY;AACd;;AAEA;EACE,eAAe;EACf,aAAa;EACb,mBAAmB;EACnB,eAAe;EACf,mBAAmB;EACnB,iBAAiB;AACnB;;AAEA;EACE,iBAAiB;AACnB;;AAEA;EACE,kBAAkB;EAClB,mBAAmB;EACnB,gBAAgB;AAClB;;AAEA;EACE,eAAe;EACf,aAAa;EACb,mBAAmB;EACnB,eAAe;EACf,0BAA0B;AAC5B;;AAEA;EACE,qBAAqB;EACrB,kBAAkB;EAClB,uBAAuB;EACvB,kBAAkB;AACpB;;AAEA;EACE,eAAe;EACf,4BAA4B;EAC5B,eAAe;EACf,uBAAuB;AACzB","sourcesContent":["@charset \"UTF-8\";\n\n@import url(\"https://fonts.cdnfonts.com/css/cascadia-code\");\n\n:root {\n  /* Logo */\n  --background: #FFFFFF;\n\n  /* Tasks title */\n  --black: #495057;\n  /* All other text/icon in tasks cards */\n  --dark-gray: #6d6a75ff;\n  /* Border of tasks card */\n  --default-gray: #adb5bdff;\n\n  /* Icons background on hover*/\n  --light-gray: #ebebeb;\n\n  /* Borders */\n  --borders-gray: #d3d3d3;\n  /* Priorities */\n  --red: #ff5a5a;\n  --yellow: #f6c364;\n  --argentinian-blue: #64b5f6ff;\n  --yellow: #f6c364;\n  --red: #ff5a5a;\n}\n\n/* CSS RESET */\n\n*,\n*::before,\n*::after {\n  box-sizing: 0;\n}\n\n* {\n  margin: 0;\n  padding: 0;\n}\n\nbody {\n  line-height: 1.5;\n  font-family: \"Cascadia Code\";\n  font-size: 100%;\n  background-color: var(--background);\n}\n\nimage,\npicture,\nsvg,\nvideo,\ncanvas {\n  display: block;\n  max-width: 100%;\n}\n\np,\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n  overflow-wrap: break-word;\n}\n\ninput,\nbutton,\ntextarea,\nselect {\n  font: inherit;\n}\n\nmain {\n  display: grid;\n  grid-template-columns: 4em 1fr 1fr;\n  grid-template-rows: 4em 1fr;\n  height: 100vh;\n}\n\nheader {\n  grid-row: 1 / 2;\n  grid-column: 1 / -1;\n  border: 2px solid var(--borders-gray);\n  display: flex;\n  justify-content: start;\n  column-gap: 1em;\n  align-items: center;\n}\n\naside {\n  grid-row: 2 / -1;\n  grid-column: 1 / 2;\n  border-right: 2px solid var(--borders-gray);\n  border-left: 2px solid var(--borders-gray);\n}\n\nnav {\n  height: 100%;\n  display: flex;\n  flex-direction: column;\n  justify-content: space-between;\n  align-items: center;\n}\n\nnav div {\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  align-items: center;\n  padding: 2rem 0;\n  gap: 1.2em;\n  font-size: 1.8rem;\n}\n\nnav div button {\n  background-color: transparent;\n  border: none;\n  color: var(--default-gray);\n}\n\nbutton#toggle-theme {\n  background: none;\n  border: none;\n  color: var(--default-gray);\n  border-radius: 5px;\n}\n\nbutton#login:hover,\nbutton#toggle-theme:hover {\n  cursor: pointer;\n  background-color: var(--light-gray);\n}\n\nnav div button i {\n  padding: 6px;\n  border-radius: 5px;\n}\n\ndiv i {\n  background-color: transparent;\n  border: none;\n  color: var(--default-gray);\n}\n\nnav div button:hover {\n  cursor: pointer;\n  background-color: rgb(235, 235, 235);\n}\n\n/* Min view */\n\n#min-view {\n  position: relative;\n  height: 100%;\n  border-right: 2px solid var(--borders-gray);\n  grid-column: 2 / 3;\n  grid-row: 2 / -1;\n  /*   display: flex;\n  flex-direction: column; */\n  grid-template-rows: repeat(auto-fit, 55px);\n}\n\n#current-tab {\n  font-size: 3rem;\n  color: var(--default-gray);\n  position: absolute;\n  bottom: 0.1em;\n  align-self: end;\n  right: 0.1em;\n  margin-right: 16px;\n}\n\n#full-view {\n  display: grid;\n  grid-row: 2 / -1;\n  grid-column: 3 / -1;\n  height: 100%;\n  grid-template-columns: 1fr;\n  grid-template-rows: min-content 1fr;\n  border-right: 2px solid var(--borders-gray);\n  align-items: start;\n  row-gap: 1em;\n}\n\n#full-view > div#empty-view {\n  color: var(--default-gray);\n  text-align: center;\n  margin-top: 45vh;\n}\n\n#logo {\n  height: 100%;\n  display: flex;\n  align-items: center;\n  padding: 0 10px;\n  gap: 10px;\n}\n\n#logo img {\n  width: 3em;\n}\n\n#logo p {\n  font-size: 2rem;\n  font-weight: 700;\n}\n\n/* TASKS CARD */\n\n.task-card {\n  margin: 1em 1em 0 1em;\n  border: 2px solid var(--default-gray);\n  border-radius: 15px;\n  display: grid;\n  grid-template-columns: 0.6em min-content 1fr min-content;\n  grid-template-rows: 1fr;\n  transition: 0.1s ease-in-out;\n}\n\n.task-card:hover {\n  cursor: pointer;\n  scale: 1.01;\n}\n\n.priority {\n  height: 100%;\n  width: 100%;\n  grid-row: 1/2;\n  grid-column: 1/2;\n  border-top-left-radius: 1em;\n  border-bottom-left-radius: 1em;\n}\n\n.checkbox-div {\n  grid-row: 1 / 2;\n  grid-column: 2 /3;\n  display: grid;\n  align-content: center;\n  justify-content: center;\n  padding: 0 1em;\n}\n\n.checkbox {\n  margin-bottom: 1px;\n  accent-color: var(--logo-blue);\n  transform: scale(1.4);\n  opacity: 60%;\n}\n\n.checkbox:hover {\n  cursor: pointer;\n  accent-color: var(--logo-blue);\n}\n\ndiv.content-wrapper {\n  grid-row: 1 / -1;\n  grid-column: 3 / 4;\n  display: flex;\n  flex-wrap: wrap;\n  padding-right: 10%;\n  justify-content: space-between;\n  align-items: center;\n}\n\n.info{\n  display: grid;\n  align-content: center;\n}\n\np.task-title {\n  font-size: 1.2em;\n  color: var(--black);\n}\n\np.project-title {\n  font-size: 0.8em;\n  color: var(--dark-gray);\n}\n\np.due-date {\n  grid-column: 4 / 5;\n  justify-self: end;\n  padding-right: 10%;\n  color: var(--dark-gray);\n  font-size: 1rem;\n}\n\n.due-date {\n  color: var(--dark-gray);\n}\n\ndiv.task-actions {\n  grid-row: 1 / 2;\n  grid-column: 4 / -1;\n  border-top-right-radius: 1em;\n  border-bottom-right-radius: 1em;\n\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  column-gap: 0.5em;\n  padding-right: 1em;\n}\n\nbutton.edit-task,\nbutton.remove-task,\nbutton.remove-project,\nbutton.edit-project {\n  background-color: transparent;\n  border: none;\n  padding: 4px 8px;\n  border-radius: 5px;\n}\n\nbutton.edit-task:hover,\nbutton.remove-task:hover,\nbutton.remove-project:hover,\nbutton.edit-project:hover{\n  cursor: pointer;\n  background-color: var(--light-gray);\n}\n\n.task-card:has(.checkbox:checked) {\n  opacity: 40%;\n}\n\n.red,\n.high {\n  background-color: var(--red);\n}\n.yellow,\n.medium {\n  background-color: var(--yellow);\n}\n.blue,\n.low {\n  background-color: var(--argentinian-blue);\n}\n\n/* MODALS */\n\n#modal-option {\n  position: fixed;\n  width: 100vw;\n  height: 100dvh;\n  z-index: 1;\n  top: 0;\n  left: 0;\n  overflow: auto;\n  background-color: rgba(0, 0, 0, 0.4);\n  display: grid;\n  place-content: center;\n}\n\n#create-option {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  row-gap: 2em;\n}\n\n#create-option > button {\n  height: 5em;\n  width: 10em;\n  background-color: white;\n  border: none;\n  border-radius: 10px;\n  font-size: 2rem;\n}\n\n#create-option > button:hover {\n  background-color: var(--argentinian-blue);\n  color: white;\n  cursor: pointer;\n}\n\n/* #modal-forms {\n  visibility: hidden;\n  height: 100dvh;\n  width: 100vw;\n  position: fixed;\n  z-index: 1;\n  top: 0;\n  left: 0;\n  overflow: auto;\n  background-color: rgb(0, 0, 0);\n  background-color: rgba(0, 0, 0, 0.4);\n  display: grid;\n  place-content: center;\n} */\n\nform {\n  background-color: #ffffff;\n  border: 2px solid var(--default-gray);\n  margin: auto;\n  padding: 20px;\n  width: 300px;\n  border-radius: 10px;\n  top: 50%;\n  left: 50%;\n  transform: translate(-50%, -50%);\n  position: fixed;\n  z-index: 1;\n}\n\n.hide {\n  display: none;\n  visibility: hidden;\n}\n\n.display {\n  display: block;\n  visibility: visible;\n}\n\n.invalid {\n  border: 2px solid var(--red);\n}\n\nform > h1 {\n  color: #6d6a75ff;\n  text-align: center;\n}\n\nform > div {\n  display: flex;\n  flex-direction: column;\n  padding: 0.5em 1em;\n  color: #495057;\n}\n\nform > div > input,\nform > div > select,\nform > div > textarea {\n  resize: none;\n  border: 2px solid #D3D3D3;\n  color: var(--dark-gray);\n  border-radius: 7px;\n  padding: 0.2em;\n}\n\nform > div > button {\n  padding: 0.4em;\n  border: none;\n  border-radius: 7px;\n  background-color: var(--argentinian-blue);\n  color: white;\n  text-align: center;\n}\n\ninput::placeholder,\ntextarea::placeholder {\n  color: #D3D3D3;\n}\n\nselect,\ninput[type=\"date\"] {\n  color: #adb5bdff;\n}\n\nbutton[type=\"submit\"]:hover {\n  cursor: pointer;\n  filter: brightness(90%);\n}\n\n/* PROJECTS CARD */\n\n.project-card {\n  margin: 1em 1em 0 1em;\n  border: 2px solid var(--default-gray);\n  border-radius: 15px;\n  display: grid;\n  grid-template-columns: 0.6em 1fr min-content;\n  grid-template-rows: 1fr;\n  transition: 0.1s ease-in-out;\n}\n\n.project-card:hover {\n  cursor: pointer;\n  transform: scale(1.01);\n}\n\n.project-stripe {\n  height: 100%;\n  width: 100%;\n  grid-row: 1/2;\n  grid-column: 1/2;\n  border-top-left-radius: 1em;\n  border-bottom-left-radius: 1em;\n  background-color: var(--default-gray);\n}\n\n.project-wrapper {\n  height: 51.98px;\n  width: 100%;\n  display: grid;\n  grid-template-columns: 1fr 1fr;\n  grid-template-rows: 1fr;\n  column-gap: 0.5em;\n  align-items: center;\n}\n\n.project-info{\n  display: grid;\n  align-content: center;\n}\n\n.project-wrapper > .project-info {\n  /*   display: flex;\n  flex-direction: column; */\n  margin-left: 0.8em;\n}\n\n.project-wrapper > .project-info > .project-title {\n  font-size: 1.2em;\n  color: var(--black);\n}\n\n.project-wrapper > .project-info > a {\n  text-decoration: none;\n  color: var(--default-gray);\n  font-size: 0.8em;\n}\n\n.project-wrapper > .project-info > a:hover {\n  text-decoration: underline black;\n}\n\n.project-actions {\n  display: flex;\n  margin-right: 1em;\n  justify-content: end;\n  column-gap: 1em;\n}\n\n.project-actions > button {\n  background-color: transparent;\n  border: none;\n  color: var(--dark-gray);\n  border-radius: 4px;\n  padding: 6px 7px;\n}\n\n.project-actions > button:hover {\n  background-color: var(--light-gray);\n}\n\n/* FULL VIEW */\n#full-project-div {\n  display: flex;\n  flex-direction: column;\n}\n\n#full-project {\n  position: relative;\n  display: grid;\n  height: min-content;\n  grid-template-rows: repeat(3, min-content);\n  grid-template-columns: 1fr;\n  padding: 1em;\n}\n\n#full-project-tasks-div {\n  height: 100%;\n  display: grid;\n  grid-template-rows: repeat(auto-fit, 80px);\n  row-gap: 1.2em;\n  overflow-x: hidden;\n}\n\n#close {\n  position: absolute;\n  right: 1em;\n  top: 0.8em;\n  font-size: 1.4em;\n  cursor: pointer;\n}\n\n#full-project-info > h1 {\n  color: var(--black);\n  font-size: 2rem;\n}\n\n#full-project-link {\n  color: var(--default-gray);\n  font-size: 1.2rem;\n  margin-top: 0.2rem;\n  text-decoration: none;\n}\n\n#full-project-link:hover {\n  text-decoration: underline;\n}\n\n#full-project-description {\n  white-space: pre-wrap;\n  padding-top: 1.5em;\n  font-size: 1.1rem;\n  color: var(--dark-gray);\n}\n\n.point {\n  color: var(--borders-gray);\n  font-size: 1.2rem;\n}\n\n.line {\n  background-color: var(--borders-gray);\n  height: 2px;\n}\n\n#full-task-div {\n  display: flex;\n  flex-direction: column;\n  font-size: 1.2rem;\n}\n\n#task-full {\n  position: relative;\n  display: grid;\n  grid-template-columns: 1fr, 5em;\n  grid-template-rows: repeat(3, min-content);\n  align-items: start;\n  padding: 1em;\n}\n\n#task-full-info {\n  grid-row: 1 / 2;\n  display: flex;\n  align-items: center;\n  column-gap: 1em;\n  c